.country{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #797982;
        border-radius: 10px;
        transition: all 1s ease-in-out;
        cursor: pointer;
}

.country:hover {
    transform: scale(1.1);
}

.country > img {
    width: 33%;
    height: auto;
}

.country > h2 {
    margin: 5px 0;
}

.desc {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1rem;
    justify-items: center;
    width: 95%;
}